<template>
  <div class="buy-page">
    <div class="buy-c">
      <p class="buy-title">
        {{$t('french.buyPage[0]')}} 
        <span class="name">{{ orderData.oop_name }}</span>
        <span class="way">{{$t('french.buyPage[1]')}}</span>
        {{ parseFloat(orderData.total_num) }} USDT
      </p>
      <div class="order clr">
        <span class="order-num">{{$t('french.buyPage[2]')}}{{ orderData.order_num }}</span>
        <span class="order-status">{{ showStatus }}</span>
      </div>
      <div class="order-detail">
        <span class="detail-list">{{$t('french.buyPage[3]')}}{{ orderData.total_price }} CNY</span>
        <span class="detail-list">{{$t('french.buyPage[4]')}}{{ parseFloat(orderData.total_num) }} USDT</span>
        <span class="detail-list">{{$t('french.buyPage[5]')}}{{ orderData.price }} CNY</span>
      </div>
      <div class="remark">
        <span>{{$t('french.buyPage[6]')}}</span>
      </div>
      <div class="contact">
        <p class="cotact-title">{{$t('french.buyPage[7]')}}</p>
        <p class="someone">{{$t('french.buyPage[8]')}}{{ orderData.oop_name }}</p>
        <p class="phone-number">{{$t('french.buyPage[9]')}}{{ orderData.oop_mobile }}</p>
      </div>
      <div class="payment-method">
        <p class="payment-title">{{$t('french.buyPage[10]')}}</p>
        <!-- 二维码 -->
        <div class="big-code" v-show="codeShow">
          <img :src="imgUrl" alt="" class="show-code" style="width: 90px; height: 90px;">
        </div>
        <template>
          <el-radio-group v-model="radio">
            <el-radio 
            :label="item.type"
            v-for="(item, i) in orderData.pay_list"
            :key="i"
            style="display: block;">
              <span class="pay-method">
                <img :src="getImg(item.type)" alt="" class="pay-img">
              </span>
              <span class="card" v-if="item.type == 'backcard'">{{$t('french.buyPage[11]')}}</span>
              <span class="card-account">{{ item.number }}</span>
              <span class="card-name">{{ item.name }}</span>
              <span class="code" @mouseenter="showCode(item.type)" @mouseleave="hiddenCode" v-if="item.type != 'backcard'">
                <img src="../../../assets/img/frenchImg/code.png" alt="" class="code-img">
              </span>
              <p class="card-detail">
                <span class="card-bank">{{ item.bank }}</span>
                <span class="bank-detail">{{ item.branch }}</span>
              </p>
            </el-radio>
          </el-radio-group>
        </template>
      </div>
      <!-- 待付款状态 -->
      <div class="wait" v-if="isShow == 1">
        <div class="wait-pay">
          <p class="countdown">{{$t('french.buyPage[12]')}}<span style="color: #6185e5;"> {{ countdowntime }} </span>{{$t('french.buyPage[13]')}}{{ orderData.oop_name }}{{$t('french.buyPage[14]')}}</p>
          <p class="pay-code">{{$t('french.buyPage[15]')}}（ {{ orderData.refer }} ）</p>
          <p class="tips-t">
            <span class="tip-icon">
              <img src="../../../assets/img/frenchImg/tips.png" alt="" class="tip-img">
            </span>
            <span class="tip-detail">
              {{$t('french.buyPage[16]')}}
              <span class="sign">"{{$t('french.buyPage[17]')}}"</span>
              {{$t('french.buyPage[18]')}}
            </span>
          </p>
          <p class="tips-b">{{$t('french.buyPage[19]')}}</p>
        </div>
        <div class="btn-group" v-show=" this.isShow != 5">
          <button class="paid-btn order-btn" @click="handlePaid">{{$t('french.buyPage[20]')}}</button>
          <button class="cancel-btn order-btn" @click="handleCancel">{{$t('french.buyPage[21]')}}</button>
        </div>
      </div>
      <!-- 已付款状态 -->
      <div class="paid" v-if="isShow == 2">
        <p class="paid-time">
          <img src="../../../assets/img/frenchImg/tips.png" alt="" class="code-img">
          <span class="paid-title">{{$t('french.buyPage[22]')}}（{{ orderData.refer }}）</span>
          <span class="code-tips">{{$t('french.buyPage[23]')}}<span class="code-time"> {{ payTime }} </span>{{$t('french.buyPage[24]')}}</span>
        </p>
      </div>
      <!-- 已完成状态 -->
      <div class="paid" v-if="isShow == 3">
        <p class="paid-time">
          <span class="paid-title">{{$t('french.buyPage[25]')}}</span>
        </p>
      </div>
      <!-- 申诉中状态 -->
      <div class="paid" v-if="isShow == 4">
        <p class="paid-time">
          <span class="paid-title">{{$t('french.buyPage[26]')}}</span>
        </p>
      </div>
      <!-- 已取消状态 -->
      <div class="paid" v-if="isShow == 5">
        <p class="paid-time">
          <span class="paid-title">{{$t('french.buyPage[27]')}}</span>
        </p>
      </div>
      <div class="caveat">
        <p class="caveat-title caveat-all">{{$t('french.buyPage[28]')}}</p>
        <p class="caveat-desc caveat-all">{{$t('french.buyPage[29]')}}</p>
        <p class="caveat-desc caveat-all">{{$t('french.buyPage[30]')}}</p>
        <p class="caveat-desc caveat-all">{{$t('french.buyPage[31]')}}</p>
      </div>
      <!-- 标记已付款弹窗 -->
      <el-dialog
        :title="diaSubmitTitle"
        :visible.sync="dialogVisible">
        <span>{{$t('french.buyPage[33]')}}</span>
        <div class="dialog-footer">
            <button class="btn dialog-btn" @click="submitPaid">{{$t('french.buyPage[34]')}}</button>
            <button class="cancel-btn dialog-btn" @click="closeDialog">{{$t('french.buyPage[35]')}}</button>
          </div>
      </el-dialog>
      <!-- 取消订单弹窗 -->
      <el-dialog
        :title="cancelTitle"
        :visible.sync="cancelDialogVisible">
        <span>{{$t('french.buyPage[37]')}}</span>
        <div class="dialog-footer">
            <button class="btn dialog-btn" @click="cancelOrder">{{$t('french.buyPage[38]')}}</button>
            <button class="cancel-btn dialog-btn" @click="closeDialog">{{$t('french.buyPage[39]')}}</button>
          </div>
      </el-dialog>
    </div>
  </div>
</template>

<script>
import { orderDetail, setOrderStatus, paymentList, reOrder } from '../../../api/c2c'
export default {
  name: 'buyPage',
  data() {
    return {
      diaSubmitTitle: this.$t('french.buyPage[32]'),
      cancelTitle: this.$t('french.buyPage[36]'),
      isShow: 1,
      tpwd: '',
      codeShow: false,
      radio: '',
      radioCancel: '1',
      radioDell: '1',
      dialogVisible: false,
      cancelDialogVisible: false,
      imgUrl: '',
      payList: [],
      orderData: {},
      showStatus: this.$t('french.buyPage[40]'),
      payType: '',
      TimeData: null,
      countdown: null,
      countdowntime: null,
      qr_time: null, // 确认支付倒计时
      payTime: null, // 渲染到页面的倒计时
      setIntervaltime: null,
      set: null, // 交易倒计时定时器
      payedSet: null // 待放行倒计时
    }
  },
  created() {
    this.setTime()
  },
  methods: {
    // 5S
    setTime() {
      var _this = this
      _this.getOrderDetail()
      _this.setIntervaltime = window.setInterval(function () {
        _this.getOrderDetail()
      }, 5000)
    },
    getOrderDetail() {
      orderDetail({ order_num: this.$route.query.order_num }).then(res=> {
        if (res.data.status == 200) {
          this.orderData = res.data.data
          this.isShow = this.orderData.status
          this.payList = res.data.data.pay_list
          this.TimeData = res.data.data.add_time
          this.countdown = res.data.data.down_time //订单倒计时
          this.qr_time = res.data.data.qr_time //确定支付倒计时
          if (this.orderData.status == 1) {
            this.showStatus = this.$t('french.buyPage[41]')
          } else if (this.orderData.status == 2) {
            this.showStatus = this.$t('french.buyPage[42]')
          } else if (this.orderData.status == 3) {
            this.showStatus = this.$t('french.buyPage[43]')
          } else if (this.orderData.status == 4) {
            this.showStatus = this.$t('french.buyPage[44]')
          } else if (this.orderData.status == 5) {
            this.showStatus = this.$t('french.buyPage[45]')
          }
          //						status = res.data.data.status;
          if (res.data.data.status == 1) {//订单倒计时
            this.chusouSet(
              res.data.data.status,
              res.data.data.down_time
            )
            return
          } else if (res.data.data.status == 2) {//待对方放币倒计时
            this.payed()
            return
          } else {
            if (this.setIntervaltime) {
              window.clearInterval(this.setIntervaltime)
            }
            if (this.set) {
              window.clearInterval(this.set)
            }
            if (this.setIntepayedSetrvaltime) {
              clearInterval(this.payedSet)
            }
            return
          }
        } else {
          this.$public.msg(res.data.msg, 'error', this)
          return
        }
      })
    },
    // 订单倒计时
    chusouSet (status, time) {
      // 需要两个参数（此时订单的状态，当前剩余时间）
      // 倒计时 执行函数
      var connet = this
      var shijian
      var minute
      connet.set = window.setInterval(()=> {
        switch (status) {
          case '1':
            // shijian = time / 60 / 60
            minute = time / 60
            connet.countdowntime =
              // parseInt(shijian) +
              // '小时' +
              parseInt(minute) +
              this.$t('french.buyPage[46]') +
              (time % 60) +
              this.$t('french.buyPage[47]')
            break;
          case '2':
            break
          default:
            window.clearInterval(connet.setIntervaltime)
            window.clearInterval(connet.set)
            break;
        }
        time = time <= 0 ? 0 : --time
        if (time == 0) {
          connet.countdowntime = this.$t('french.buyPage[48]') + '...';
          clearInterval(connet.set)
          // 倒计时结束未付款，跳转取消订单
          // this.isShow = 5
          this.showStatus = this.$t('french.buyPage[45]')
        }
      }, 1000)
    },
    // 待自动确认倒计时
    payed () {
      var _this = this
      //关闭交易倒计时 执行这个倒计时时说明交易倒计时已不存在
      window.clearInterval(_this.set)
      //为了防止意外多起定时器 先把自己给干掉
      window.clearInterval(_this.payedSet)
      _this.payedSet = setInterval(() => {
        // var shi = _this.qr_time / 60 / 60
        var minute = _this.qr_time / 60
        _this.payTime =
          // parseInt(shi) +
          // '小时' +
          parseInt(minute) +
          this.$t('french.buyPage[49]') +
          (_this.qr_time % 60) +
          this.$t('french.buyPage[50]')
        _this.qr_time--
        if (_this.qr_time < 0) {
          _this.qr_time = 0
          _this.payTime = this.$t('french.buyPage[51]') + '...';
          clearInterval(_this.payedSet)
        }
      }, 1000)
    },
    // 获取照片地址
    getImg(type) {
      if (type == 'wx') {
        return require('../../../assets/img/frenchImg/wechat.png')
      } else if (type == 'alipay') {
        return require('../../../assets/img/frenchImg/alipay.png')
      } else if (type == 'backcard') {
        return require('../../../assets/img/frenchImg/bank.png')
      }
    },
    // 显示二维码
    showCode(type) {
      this.codeShow = true
      for (var i=0; i<this.payList.length; i++) {
        if (type == this.payList[i].type) {
          this.imgUrl = this.payList[i].img
        }
      }
    },
    // 隐藏二维码
    hiddenCode() {
      this.codeShow = false
    },
    // 标记已付款
    handlePaid() {
      if (this.radio == '') {
        this.$public.msg(this.$t('french.buyPage[52]'),'error',this)
        return
      } else {
        this.dialogVisible = true
      } 
    },
    // 确认标记已付款
    submitPaid() {
      if (this.radio == 'wx') {
        this.payType = '1'
      } else if (this.radio == 'alipay') {
        this.payType = '2'
      } else if (this.radio == 'backcard') {
        this.payType = '3'
      }
      setOrderStatus({ 
        pay_type: this.payType, 
        order_no: this.$route.query.order_num }).then(res=> {
        if (res.data.status == 200) {
          window.clearInterval(this.payedSet)
          this.isShow = 2
          this.getOrderDetail()
          this.dialogVisible = false
        } else {
          this.$public.msg(res.data.msg, 'error', this)
          return
        }
      })
    },  
    // 取消订单
    handleCancel() {
      this.cancelDialogVisible = true
    },
    // 确认取消订单
    cancelOrder() {
      reOrder({ order_num: this.$route.query.order_num }).then(res=> {
        if (res.data.status == 200) {
          this.$public.msg(this.$t('french.buyPage[53]'), 'success', this)
          window.clearInterval(this.payedSet)
          this.isShow = 5
          this.getOrderDetail()
          this.cancelDialogVisible = false
        } else {
          this.$public.msg(res.data.msg, 'success', this)
        }
      })
    },
    // 清除定时器
    clearTimer() {
     
    },
    // 关闭弹窗
    closeDialog() {
      this.dialogVisible = false
      this.cancelDialogVisible = false
    },
    offTime() {
      var connet = this;
      window.clearInterval(connet.setIntervaltime);
      window.clearInterval(connet.set);
      window.clearInterval(connet.payedSet);
    }
  },
  // 离开页面销毁定时器
  beforeDestroy(to, from, next) {
    this.offTime()
    next()
  }
}
</script>

<style lang="less">
   @import '../../../assets/index.less';
   @import 'buyPage.less';
</style>


